.community-tabs {
    display: flex;
    flex-direction: column;
}

.community-tabs-header-row {
    background-color: #4f17a7;
    min-height: 200px;
    color: #fff;
    display: flex;
    align-items: center;
    padding-left: 55px;
}

.side-nav-ctn {
    background-color: #fff;
    color: #37424a;
    font-family: Agrandir,helvetica,arial,sans-serif;
    max-height: 90vh;
    overflow-y: auto;
}

.side-nav-ctn a {
    text-decoration: none;
    color: #4f17a7;
}

.underlined {
    display: block;
    position: relative;
    padding-bottom: 15px;
    margin-bottom: 20px;
    border-bottom: 1px solid #f5f5f5;
}

.underlined:before {
    content: '';
    display: block;
    width: 16px;
    height: 3px;
    position: absolute;
    margin-top: 2px;
    left: 0;
    bottom: -1.5px;
    background-color: #4f17a7;
}

.side-nav-ctn-outer {
    background-color: #f5f5f5;
    // max-width: 35vh;
    // padding: 35px 25px 35px 70px;
}

.side-nav-links-header {
    font-size: 1.6rem;
    line-height: 2.4rem;
}

.side-nav-item-link-ctn {
    display: block;
    margin-top: 20px;
}

.side-nav-item-link.selected {
    color: #4f17a7;
    border-color: #4f17a7;
}

.side-nav-item-link {
    display: inline;
    padding-bottom: 3px;
    line-height: 2.5rem;
    color: #37424a;
    border-bottom: 1px solid transparent;
}    

@media all and (min-width:577px) {            
    .community-tabs-ctn {
        display: flex;
        flex-direction: row;
    }
    
    .community-content {
        min-width: 70vw;
        padding: 15px;
    }

    .side-nav-ctn {
        align-items: flex-start;
        min-width: 250px;
        padding-left: 15px;
        border-radius: 4px;
    }   
    
    .side-nav-ctn a {
        align-items: flex-start;
        text-transform: none;
        margin-left: -5px;
    }
    
    .side-nav-ctn-outer {
        padding: 35px 25px 15px 50px;
    }   

    .side-nav-item-link-ctn {
        margin-left: 15px;
    }    
}

@media all and (max-width:576px) {
    .community-tabs-header-row {
        padding: 0 50px;
        min-height: 100px;
    }

    .community-tabs-ctn {
        display: flex;
        flex-direction: column;
    }
    
    .community-content {
        width: 96vw;
        padding-top: 15px;
    }
    
    .side-nav-ctn a {
        text-transform: none;
    }
}
